import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {
  API_STORAGELOCATIONS_RESERVOIRAREAS,
  API_STOCKRELOCATION_SHIPPERKEYVALUES,
  API_PRODUCTS_KEYVALUES,
  API_PRODUCT_CATEGORYSONS,
  API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
import {getNowPageId} from '../../global/components/menuData'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.inventoryDetail, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {
        storeLocationCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        reservoirAreaCode,
        diffAmount,
        initDate,
        shipperCode,
        productCode,
        productName,
        orderCode,
        remark
      },
      userLoation,
      manageInventoryDetailAdd
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
              label='货主'
              selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
              label='库区'
              selectItem={{data: reservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>储位编码：</label>
              <div className='box'>
                <Input value={storeLocationCode} onChange={(e) => {
                  this.onInputChange({storeLocationCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          {/* <Col span={8}>
            <CsxSelect
              dateApi={API_INVENTORY_KEYVALUES}
              label='是否盘差'
              selectItem={{data: isDiff, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({isDiff: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>差异数量范围：+-</label>
              <div className='box'>
                <Input value={diffQty} onChange={(e) => {
                  this.onInputChange({diffQty: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>差异金额范围：+-</label>
              <div className='box'>
                <Input value={diffAmount} onChange={(e) => {
                  this.onInputChange({diffAmount: e.target.value})
                }}/>
              </div>
            </div>
          </Col> */}
          {/* <Col span={8}>
            <div className='commonInput'>
              <label>商品：</label>
              <div className='box'>
                <Input value={product} placeholder='商品编码/条码/商品名称' onChange={(e) => {
                  this.onInputChange({product: e.target.value})
                }} />
              </div>
            </div>
          </Col> */}
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>盘点日期：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>单号：</label>
              <div className='box'>
                <Input placeholder='批量单号查询，请用逗号隔开' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>备注：</label>
              <div className='box'>
                <Input value={remark} onChange={(e) => {
                  this.onInputChange({remark: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={18}>
            <Row type="flex" justify="start">
              <CsxSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxSelect
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                errMessage = '请先选择大类'
                style={{width: 163, marginLeft: 10}}
              />
              <CsxSelect
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                errMessage = '请先选择中类'
                style={{width: 163, marginLeft: 10}}
              />
            </Row>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              {
                manageInventoryDetailAdd && <Button type="primary" className={style.mr10} onClick={() => {
                  this.props.update({isShowModal: true, modalData: {}})
                }} >新增</Button>
              }
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



