import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'
import {API_INVENTORY_IMPORT, API_CAISX_B2B_WMS_INVENTORYMULTIPLELOCATIONIMPORT} from 'api'
import {Import} from 'HOC'

@connect(
  state => ({...state.inventoryDetail}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    handleExport({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {handleGetList, searchParams, page, size, update} = this.props
    res && res.message === 'ok' && update({resultModalDataList: res.result.data, isShowResultModal: true})
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }
  render() {
    const {multiSiteImportBottonPower, manageInventoryDetailImport, manageInventoryDetailExport} = this.props
    return (
      <div className={style['table-tool']}>
        <Row>
          <Col span={12}>
            <Row type="flex" justify="start">
              <Button type="primary" className={style.btn} onClick={() => {
                this.handleDownLoad('T03')
              }}>模板下载</Button>
              {
                manageInventoryDetailImport && <Import
                  action={{api: API_INVENTORY_IMPORT}}
                  onOk={this.handleImportSuccess}
                  onExprotError={this.handleExprotError}
                  beforeImport={() => {
                    const {update} = this.props
                    update({tableLoading: true})
                  }}
                  onResponse={() => {
                    const {update} = this.props
                    update({tableLoading: false})
                  }}
                >
                  <Button type="primary" className={style.btn}>导入</Button>
                </Import>
              }
              {
                manageInventoryDetailExport && <Button type="primary" className={style.btn} onClick={() => {
                  this.handleExport()
                }}>导出Excel</Button>
              }
            </Row>
          </Col>
          <Col span={12}>
            { multiSiteImportBottonPower &&
              <Row type="flex" justify="end">
                <Button type="primary" className={style.btn} onClick={() => {
                  this.handleDownLoad('T09')
                }}>多地点盘点导入模板</Button>
                <Import
                  action={{api: API_CAISX_B2B_WMS_INVENTORYMULTIPLELOCATIONIMPORT}}
                  onOk={this.handleImportSuccess}
                  onExprotError={this.handleExprotError}
                  beforeImport={() => {
                    const {update} = this.props
                    update({tableLoading: true})
                  }}
                  onResponse={() => {
                    const {update} = this.props
                    update({tableLoading: false})
                  }}
                >
                  <Button type="primary" className={style.btn}>多地导入</Button>
                </Import>
              </Row>
            }
          </Col>
        </Row>
      </div>
    )
  }
}



