import React, { PureComponent } from 'react'
import {
  Table
} from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.inventoryDetail, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: 'ID',
      width: 150,
      dataIndex: 'inventoryCode',
      key: 'inventoryCode'
    }, {
      title: '大类',
      width: 150,
      dataIndex: 'bigCategoryCode',
      key: 'bigCategoryCode',
      render: (text, record) => <span>{`${record.bigCategoryCode}-${record.bigCategoryName}`}</span>
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '从库区',
      width: 100,
      dataIndex: 'reservoirAreaCode',
      key: 'reservoirAreaCode'
    }, {
      title: '从储位',
      width: 150,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode'
    }, {
      title: '盘点批次价',
      width: 100,
      dataIndex: 'lastInPrice',
      key: 'lastInPrice'
    }, {
      title: '盘点批次价（不含税）',
      width: 100,
      dataIndex: 'lastInPriceNoTax',
      key: 'lastInPriceNoTax'
    }, {
      title: '盘点数量',
      width: 100,
      dataIndex: 'inventoryQty',
      key: 'inventoryQty'
    }, {
      title: '储位库存',
      width: 100,
      dataIndex: 'systemStoreQty',
      key: 'systemStoreQty'
    }, {
      title: '盘点金额',
      width: 100,
      dataIndex: 'inventoryAmount',
      key: 'inventoryAmount'
    }, {
      title: '盘点差异',
      width: 100,
      dataIndex: 'inventoryQtyDiff',
      key: 'inventoryQtyDiff'
    }, {
      title: '盘点差异金额',
      width: 100,
      dataIndex: 'inventoryAmountDiff',
      key: 'inventoryAmountDiff'
    },{
      title: '盘点差异金额（不含税）',
      width: 100,
      dataIndex: 'inventoryAmountDiffNoTax',
      key: 'inventoryAmountDiffNoTax'
    }, {
      title: '是否过账',
      width: 100,
      dataIndex: 'postingFlag',
      key: 'postingFlag'
    }, {
      title: '操作人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '盘点时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '盘点过账时间',
      width: 150,
      dataIndex: 'passBillTime',
      key: 'passBillTime'
    }, {
      title: '备注',
      width: 150,
      dataIndex: 'remark',
      key: 'remark'
    }]
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: true, columns: sorter.field } })
      } else {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: false, columns: sorter.field } })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({ searchParams: { page: pagination.current, size: pagination.pageSize, ...searchParams } })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: 'max-content', y: 400 }}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



