import * as actionType from './actionType'
import {getData} from 'util'
import {API_INVENTORY_DETAILS_PAGE, API_INVENTORY_EXPORT, API_INVENTORY_INVENTORYPRODUCTINFO, API_INVENTORY_ADDINVENTORY, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.INVENTORYDETAIL_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.INVENTORYDETAIL_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_INVENTORY_DETAILS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.INVENTORYDETAIL_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_INVENTORY_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 获取储位库存
export const handleGetGoodsInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.INVENTORYDETAIL_UPDATE, data: {createTableLoading: true}})
  let api = API_INVENTORY_INVENTORYPRODUCTINFO
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.INVENTORYDETAIL_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 创建
export const handleCommit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.INVENTORYDETAIL_UPDATE, data: {tableLoading: true, modalTuttonLoading: true}})
  let api = API_INVENTORY_ADDINVENTORY
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.INVENTORYDETAIL_UPDATE,
      data: {
        tableLoading: false,
        modalTuttonLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}

// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    console.log(Power)

    dispatch({
      type: actionType.INVENTORYDETAIL_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.INVENTORYDETAIL_FILE
    })
  })
}



