import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  message
} from 'antd'
import {CsxFormSelect, CsxFormSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES, API_INVENTORY_STORAGEKEYVALUE} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.inventoryDetail}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {

  }
  handleSubmit = (e) => {
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowResultModal: false})
  }
  render() {
    const {resultModalDataList = []} = this.props
    console.log('resultModalDataList is', resultModalDataList)
    return (
      <Modal
        title={'导入成功'}
        visible={true}
        onCancel={this.handleCloseModal}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='666px'
      >
        <div style={{
          marginBottom: '18px',
          maxHeight: '300px',
          wordBreak: 'break-all',
          overflow: 'auto'
        }}>
          创建盘点单号：
          {
            // resultModalDataList.toString(',')
            String(resultModalDataList ? resultModalDataList :'')
          }
        </div>
        <Row
          type="flex"
          justify="center"
        >
          <Col>
            <Button type="primary" onClick={this.handleCloseModal}>我知道了</Button>
          </Col>
        </Row>
      </Modal>
    )
  }
}
export default Form.create()(App)



