import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  message
} from 'antd'
import {CsxFormSelect, CsxFormSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES, API_INVENTORY_STORAGEKEYVALUE} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.inventoryDetail}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {

  }
  handleSelectChange = (value) => {
    const {update, modalData} = this.props
    // form.setFieldsValue({
    //   ...value
    // })
    update({modalData: {...modalData, ...value}})
  }
  handleSubmit = (e) => {
    const {form, modalData, handleCommit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleCommit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
          this.handleCloseModal()
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    return (
      <Modal
        title={'新建库存盘点'}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='666px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="货主"
                hasFeedback
              >
                {getFieldDecorator('shipperCode', {
                  initialValue: modalData.shipperCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                    selectItem={{key: 'key', valueKey: 'value'}}
                    handleSelectChange={(value) => {
                      this.handleSelectChange({shipperCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="盘点储位"
                hasFeedback
              >
                {getFieldDecorator('storeLocationCode', {
                  initialValue: modalData.storeLocationCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSearch
                    dateApi={API_INVENTORY_STORAGEKEYVALUE}
                    searchParamsKey='storeLocationCode'
                    selectItem={{data: modalData.storeLocationCode, key: 'key', valueKey: ['key', 'value']}}
                    onChangeSelect={(value) => {
                      this.handleSelectChange({storeLocationCode: value})
                    }}
                    onSelect={(value) => {
                      this.handleSelectChange({storeLocationCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="商品"
              >
                {getFieldDecorator('productCode', {
                  initialValue: modalData.productCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSearch
                    dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
                    searchParamsKey='productCode'
                    searchParams={{shipperCode: modalData.shipperCode && modalData.shipperCode.value}}
                    selectItem={{data: modalData.productCode, key: 'key', valueKey: ['key', 'value']}}
                    onChangeSelect={(value) => {
                      this.handleSelectChange({productCode: value})
                    }}
                    onSelect={(value) => {
                      const {handleGetGoodsInfo} = this.props
                      if (!modalData.storeLocationCode || !modalData.storeLocationCode.value) {
                        message.error('请先选择盘点储位')
                        this.handleSelectChange({productCode: ''})
                        return
                      }
                      this.handleSelectChange({productCode: value})
                      handleGetGoodsInfo({searchParams: {productCode: value.value, shipperCode: modalData.shipperCode && modalData.shipperCode.value, storeLocationCode: modalData.storeLocationCode && modalData.storeLocationCode.value}, errMessage: '请先选择货主，盘点储位'}, (data) => {
                        this.handleSelectChange({storyNum: data.qty, unit: data.unit})
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="储位库存"
              >
                {getFieldDecorator('storyNum', {
                  initialValue: modalData.storyNum
                })(
                  <label>{modalData.storyNum}</label>
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="盘点数量"
                hasFeedback
              >
                {getFieldDecorator('inventoryQty', {
                  initialValue: modalData.inventoryQty,
                  rules: [{
                    required: true, message: '请输入正确的数量', pattern: (modalData.unit && modalData.unit.toUpperCase()) == 'KG' ? /^(\d|[1-9]\d*)(\.?\d{1,3})?$/ : /^(0|([1-9]\d*))$/
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
            <Col span={12}>
              <FormItem
                {...formItemLayout}
                label="备注"
                hasFeedback
              >
                {getFieldDecorator('remark', {
                  initialValue: modalData.remark
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



